import TruffleContract from 'truffle-contract';
import _ from 'lodash';
import AchievementTopNDataContract from '../contracts/AchievementTopNData.json';
import { getProviderAndAccounts } from '../helpers/web3Helpers';
import { getWeb3 } from "../helpers/getWeb3";

export async function achievementTopNDataContract() {
    const contract = TruffleContract(AchievementTopNDataContract);
    const { currentProvider, accounts } = await getProviderAndAccounts();
    contract.setProvider(currentProvider);
    contract.defaults({from: accounts[0]});
    return contract.deployed();
}

export async function achievementTopN() {
    const contract = await achievementTopNDataContract();
    const datas = await contract.topN();
    const topIntroduces = datas[0];
    const topAchievements = datas[1];
    var rankInfos = [];
    for(var i = 0; i < topIntroduces.length; i++) {
        var rankInfo = {};
        var account = topIntroduces[i];
        if (account === '0x0000000000000000000000000000000000000000') {
            rankInfo.account = '0x0';
        } else {
            rankInfo.account = account.slice(0, 8).concat('...').concat(account.slice(34));
        }
        rankInfo.amount = _.ceil(web3.fromWei(topAchievements[i].toNumber(), 'ether'), 3);
        rankInfos.push(rankInfo);
    }
    return rankInfos;
}

export async function weekAchievementData() {
    const contract = await achievementTopNDataContract();
    const { accounts } = await getProviderAndAccounts();
    const web3 = await getWeb3();
    const achievement = await contract.weekData();
    var rankInfo = {};
    rankInfo.account = accounts[0];
    rankInfo.amount = _.ceil(web3.fromWei(achievement.toNumber(), 'ether'), 3);
    return rankInfo;
}
