import TruffleContract from 'truffle-contract';

import IntroduceContract from '../contracts/Introduce.json';
import { getProviderAndAccounts } from '../helpers/web3Helpers';

export async function introduceContract() {
    const contract = TruffleContract(IntroduceContract);
    const { currentProvider, accounts } = await getProviderAndAccounts();
    contract.setProvider(currentProvider);
    contract.defaults({from: accounts[0]});
    return contract.deployed();
}

export async function myInviteCode() {
    const contract = await introduceContract();
    return contract.myInviteCode();
}

export async function myIntroducer() {
    const contract = await introduceContract();
    return contract.myIntroducer();
}

export async function createInviteCode() {
    const contract = await introduceContract();
    return contract.createInviteCode();
}

export async function setInviteCode(inviteCode) {
    const contract = await introduceContract();
    return contract.setInviteCode(inviteCode);
}

export async function myIntroduceInfo() {
    const contract = await introduceContract();
    return contract.myIntroduceInfo();
}

