import TruffleContract from 'truffle-contract';
import _ from 'lodash';
import BetValueTopNDataContract from '../contracts/BetValueTopNData.json';
import { getProviderAndAccounts } from '../helpers/web3Helpers';
import { getWeb3 } from "../helpers/getWeb3";

export async function betValueTopNDataContract() {
    const contract = TruffleContract(BetValueTopNDataContract);
    const { currentProvider, accounts } = await getProviderAndAccounts();
    contract.setProvider(currentProvider);
    contract.defaults({from: accounts[0]});
    return contract.deployed();
}

export async function betTopN() {
    const contract = await betValueTopNDataContract();
    const datas = await contract.topN();
    const topPlayers = datas[0];
    const topBets = datas[1];
    var rankInfos = [];
    for(var i = 0; i < topPlayers.length; i++) {
        var rankInfo = {};
        var account = topPlayers[i];
        if (account === '0x0000000000000000000000000000000000000000') {
            rankInfo.account = '0x0';
        } else {
            rankInfo.account = account.slice(0, 8).concat('...').concat(account.slice(34));
        }
        rankInfo.amount = _.ceil(web3.fromWei(topBets[i].toNumber(), 'ether'), 3);
        rankInfos.push(rankInfo);
    }
    return rankInfos;
}

export async function weekBetData() {
    const contract = await betValueTopNDataContract();
    const { accounts } = await getProviderAndAccounts();
    const web3 = await getWeb3();
    const bet = await contract.weekData();
    var rankInfo = {};
    rankInfo.account = accounts[0];
    rankInfo.amount = _.ceil(web3.fromWei(bet.toNumber(), 'ether'), 3);
    return rankInfo;
}
