import TruffleContract from 'truffle-contract';
import BigNumber from "bignumber.js";
import {getLang} from "@web3game/lang";

import PersonalCenterContract from '../contracts/PersonalCenter.json';
import { getProviderAndAccounts } from '../helpers/web3Helpers';
import { getWeb3 } from "../helpers/getWeb3";
import _ from 'lodash';
import moment from 'moment';

import TaskImage1 from '../../home/styles/app/myTask/1@3x.png';
import TaskImage2 from '../../home/styles/app/myTask/2@3x.png';
import TaskImage3 from '../../home/styles/app/myTask/3@3x.png';
import TaskImage4 from '../../home/styles/app/myTask/4@3x.png';
const TaskImages = {
    TaskImage1,
    TaskImage2,
    TaskImage3,
    TaskImage4,
};

export async function personalCenterContract() {
    const contract = TruffleContract(PersonalCenterContract);
    const { currentProvider, accounts } = await getProviderAndAccounts();
    contract.setProvider(currentProvider);
    contract.defaults({from: accounts[0]});
    return contract.deployed();
}

function totalProfit(tspf, tf) {
    if (!tf) {
        return tspf;
    }
    return tspf.plus(tf);
}

function myScroll(scrollType, scrollData, tasks, latestGain, web3) {
    var ids = scrollData[0];
    if(ids.length === 0) {
        return latestGain;
    }

    var name;
    var rate;
    var times;
    switch(scrollType) {
        case 0:
            name = getLang().HOME_PAGE.PRODUCT_NAME_0;
            rate = 6;
            times = 30;
            break;
        case 1:
            name = getLang().HOME_PAGE.PRODUCT_NAME_1;
            rate = 8;
            times = 30;
            break;
        case 2:
            name = getLang().HOME_PAGE.PRODUCT_NAME_2;
            rate = 15;
            times = 30;
            break;
        case 3:
            name = getLang().HOME_PAGE.PRODUCT_NAME_3;
            rate = 20;
            times = 30;
            break;
        default:
    }

    for(var i = 0; i < ids.length; i++) {
        var id = ids[i];
        // 如果id=0,说明任务已经被删除
        if(id.toNumber() === 0) {
            continue;
        }
        
        var task = {};
        task.scrollType = scrollType;
        task.id = id;
        task.name = name;
        task.rate = rate;
        task.times = times;
        task.remainTimes = scrollData[1][i].toNumber();
        task.latestGain = scrollData[2][i];
        if(task.latestGain.toNumber() > latestGain.toNumber()) {
            latestGain = task.latestGain;
        }
        task.tti = scrollData[3][i];
        task.staticProfitOfOneTime = _.ceil(web3.fromWei(scrollData[4][i].toNumber(), 'ether'), 3);
        task.staticProfit = _.ceil(web3.fromWei(scrollData[5][i].toNumber(), 'ether'), 3);
        tasks.push(task);
    }
    return latestGain;    
}

export async function myScrolls() {
    const web3 = await getWeb3();
    const contract = await personalCenterContract();
    const scrollData0 = await contract.myScrolls(0);
    const scrollData1 = await contract.myScrolls(1);
    const scrollData2 = await contract.myScrolls(2);
    const scrollData3 = await contract.myScrolls(3);

    var myTaskResponse = {};

    // 累加总收益
    var tspf = new BigNumber(0);
    tspf = totalProfit(tspf, scrollData0[6]);
    tspf = totalProfit(tspf, scrollData1[6]);
    tspf = totalProfit(tspf, scrollData2[6]);
    tspf = totalProfit(tspf, scrollData3[6]);

    // 任务列表
    var tasks = [];
    var latestGain = new BigNumber(0);
    latestGain = myScroll(0, scrollData0, tasks, latestGain, web3);
    latestGain = myScroll(1, scrollData1, tasks, latestGain, web3);
    latestGain = myScroll(2, scrollData2, tasks, latestGain, web3);
    latestGain = myScroll(3, scrollData3, tasks, latestGain, web3);

    myTaskResponse.todayTotalIncome = _.ceil(web3.fromWei(tspf.toNumber(), 'ether'), 3);
    myTaskResponse.taskList = tasks;
    myTaskResponse.latestGain = latestGain.eq(0) ? '无' : moment(new Date(latestGain.toNumber() * 24 * 60 * 60 * 1000)).format('YYYY-MM-DD');
    return myTaskResponse;
}

export async function getQuarterRewards() {
    const web3 = await getWeb3();
    const contract = await personalCenterContract();
    const datas = await contract.getQuarterRewards();
    const enddays = datas[0];
    const rewards = datas[1];
    const availableRewards = datas[2];
    var result = {};
    var quarters = [];
    if(enddays.length === 0) {
        return result;
    }
    for(var i = 0; i < enddays.length; i++) {
        var quarter = {};
        quarter.quarterName = getLang().HOME_PAGE.QUARTER + ' ' + (i + 1);
        quarter.endday = moment(new Date(enddays[i].toNumber() * 24 * 60 * 60 * 1000)).format('YYYY-MM-DD');
        quarter.reward = _.ceil(web3.fromWei(rewards[i].toNumber(), 'ether'), 3);
        quarter.availableReward = _.ceil(web3.fromWei(availableRewards[i].toNumber(), 'ether'), 3);
        quarters.push(quarter);
    }
    result.availableTotal = _.ceil(web3.fromWei(datas[3].toNumber(), 'ether'), 3);
    result.bonusList = quarters;
    return result;
}

export async function drawQuarterRewards() {
    const contract = await personalCenterContract();
    return contract.drawQuarterRewards();
}

export async function scrolls() {
    const contract = await personalCenterContract();
    const datas = await contract.scrolls();
    var ss = [];
    for(var i = 0; i < datas[0].length; i++) {
        var scroll = {};
        scroll.id = i;
        scroll.tti = _.ceil(web3.fromWei(datas[0][i].toNumber(), 'ether'), 3);
        scroll.rate = datas[1][i].toNumber();
        scroll.limit = datas[2][i].toNumber();
        scroll.times = datas[3][i].toNumber();
        scroll.canBuy = datas[4][i].toNumber();
        if(i == 0) {
            scroll.name = getLang().HOME_PAGE.PRODUCT_NAME_0;
            scroll.imageUrl = TaskImages.TaskImage1;
        } else if (i == 1) {
            scroll.name = getLang().HOME_PAGE.PRODUCT_NAME_1;
            scroll.imageUrl = TaskImages.TaskImage2;
        } else if (i == 2) {
            scroll.name = getLang().HOME_PAGE.PRODUCT_NAME_2;
            scroll.imageUrl = TaskImages.TaskImage3;
        } else if (i == 3) {
            scroll.name = getLang().HOME_PAGE.PRODUCT_NAME_3;
            scroll.imageUrl = TaskImages.TaskImage4;
        }
        ss.push(scroll);
    }
    return ss;
} 